.control {
  span {
    color: @brand-white;
    background-color: rgba(0, 0, 0, 0.45);
    text-align: center;
    height: 57px;
    width: 57px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.vertical-line {
  background-color: rgba(0, 0, 0, 0.45);
  width: 13px;
  height: 57px;
}

.control.right {
  i{
    margin-right: 12px;
  }
  .vertical-line {
    margin-right: 5px;
  }
}

.control.left {
  i{
    margin-left: 12px;
  }
  .vertical-line {
    margin-left: 5px;
  }
}

.bg-danger .nav {

  li {
    .active > a {

    span {
      border-bottom: 3px solid #fff;
    }
    }
    margin-right: 3em;
  }
}


.text-vertical-line{
  width: 5px;
  height: 26px;
  background-color: #e2222d;
}

.text-vertical-line-lg{
  width: 14px;
  height: 57px;
  background-color: #e2222d;
}

a{
  transition: all ease-in-out 600ms;
  &.bg-danger:hover{
    color: @brand-white;
    text-decoration: overline;
  }
}


#content, #content img {
  animation: fadein 480ms;
}

@keyframes fadein {
  0% {
    filter: blur(6px);
    opacity: 0.4;
  }
  25% {
    filter: blur(5px);
    opacity: 0.55;
  }
  25% {
    filter: blur(4px);
    opacity: 0.70;
  }
  100% {
    filter: blur(3px);
    opacity: 0.85;
  }
}

@-moz-keyframes fadein { /* Firefox */
  0% {
    filter: blur(6px);
    opacity: 0.4;
  }
  25% {
    filter: blur(5px);
    opacity: 0.55;
  }
  25% {
    filter: blur(4px);
    opacity: 0.70;
  }
  100% {
    filter: blur(3px);
    opacity: 0.85;
  }
}

@-webkit-keyframes fadein { /* Safari and Chrome */
  0% {
    filter: blur(6px);
    opacity: 0.4;
  }
  25% {
    filter: blur(5px);
    opacity: 0.55;
  }
  25% {
    filter: blur(4px);
    opacity: 0.70;
  }
  100% {
    filter: blur(3px);
    opacity: 0.85;
  }
}

@-o-keyframes fadein { /* Opera */
  0% {
    filter: blur(6px);
    opacity: 0.4;
  }
  25% {
    filter: blur(5px);
    opacity: 0.55;
  }
  25% {
    filter: blur(4px);
    opacity: 0.70;
  }
  100% {
    filter: blur(3px);
    opacity: 0.85;
  }
}